* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	background-color: rgba(0, 0, 0, 0.801);
	font-family: 'Titillium Web';
	font-size: 20px;
	font-weight: 300;
	text-rendering: optimizeLegibility;
	scroll-behavior: smooth;
}

/* -------------------------------------------- */
/* ------------- HEADER -----------------*/
/* --------------------------------------------*/

header {
	background-image: linear-gradient(
			rgba(0, 0, 0, 0.801),
			rgba(189, 31, 31, 0.795)
		),
		url('img/CCScreens.jpg');
	background-size: cover;
	background-position: unset;
	height: 100vh;
	background-attachment: fixed;
}

h1 {
	color: #fff;
	font-size: 80px;
	text-align: center;
	margin-top: 15%;
}
h2 {
	color: azure;
	margin-left: 25px;
}

.row {
	max-width: 1704px;
}
.main-nav {
	color: rgb(169, 226, 157);
}

.main-nav img {
	width: 75px;
	border-radius: 50%;
	float: left;
	margin-left: 50px;
	margin-top: 20px;
}

.main-nav li {
	float: right;
	margin: 20px;
	text-transform: uppercase;
	list-style-type: none;
}

.hero-text-box {
	color: green;
	padding-top: 10px;
}

/* -------------------------------------------- */
/* ---------------- LINKS ------------------*/
/* --------------------------------------------*/

a:link,
a:visited {
	color: rgb(223, 223, 223);
	text-decoration: none;
	transition: border-bottom 4s, color 0.4s;
}

a:hover,
a:active {
	color: rgb(185, 56, 56);
	border-bottom: 2px solid transparent;
}

/* -------------------------------------------- */
/* -------------- UNDER CONSTRUCTION --------------*/
/* --------------------------------------------*/

.under {
	display: block;
	padding-top: 50px;
	padding-left: 400px;
	background-color: maroon;
	color: mintcream;
	font-weight: 700;
	justify-items: center;
}

.under a {
	color: rgb(19, 226, 46);
}

.under img {
	width: 500px;
	height: auto;
	padding: 60px 0 60px 0;
	margin-left: 250px;
}

/* -------------------------------------------- */
/* -------------- BUTTONS --------------*/
/* --------------------------------------------*/

.hero-box-btn {
	display: block;
	background-color: rgba(27, 27, 27, 0.521);
	border-radius: 200px;
	width: 175px;
	height: auto;
	font-size: 30px;
	text-align: center;
	margin-left: 25%;
	margin-top: 55px;
}

.hero-box-btn:link,
.hero-box-btn:visited,
input[type='submit'] {
	display: inline-block;
	padding: 10px 30px;
	font-weight: 300;
	text-decoration: none;
}

/* -------------------------------------------- */
/* -------------- GAMES --------------*/
/* --------------------------------------------*/

.section-games {
	height: 600px;
}

