/* -------------------------------------------- */
/* -------------- PROJECTS --------------*/
/* --------------------------------------------*/

.videos {
	background-color: rgb(122, 46, 46);
	padding-top: 20px;
	padding-left: 200px;
}

.vid-omni {
	margin-left: 20px;
}

/* -------------------------------------------- */
/* -------------- EMAIL LIST --------------*/
/* --------------------------------------------*/

#mc_embed_signup {
	background-image: linear-gradient(
			rgba(190, 91, 91, 0.801),
			rgba(190, 91, 91, 0.801)
		),
		url(/img/dark_logo_white_background.jpg);
	background-size: cover;
}

.email-button {
	background-color: #bebe5b;
}

/* -------------------------------------------- */
/* -------------- FOOTER --------------*/
/* --------------------------------------------*/

footer {
	display: flex;
	height: 5rem;
	width: 100vw;
	overflow: hidden;
	justify-content: space-evenly;
	background-color: rgb(83, 83, 83);
	padding-top: 1rem;
}

.foot-links a {
	padding-top: 0.7rem;
	padding-right: 1rem;
}

.copy {
	padding-top: 1rem;
}

footer img {
	width: 1.7rem;
	padding: 0.3rem;
}

.social-media a:link,
.social-media a:visited,
.social-media a:hover,
.social-media a:active {
	border-bottom: none;
}

.attribute {
	padding-top: 0.5rem;
	font-size: 0.5rem;
}
